.taskToggleButton {
    position : absolute;
    top: 100%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    width: 150px;
    height: 60px;
    color : white;
    background: #004d80;
    border-radius: 30px;
    font-size: 20px;
    -webkit-transition: background 0.25s, color 0.5s, font-size 0.25s;
    -o-transition: background 0.25s, color 0.5s, font-size 0.25s;
    transition: background 0.25s, color 0.5s, font-size 0.25s;
  }
  
  .taskToggleButton:hover {
    background: #fff;
    color: #004d80;
    font-size: 25px;
  }
  
  .taskToggleButton:focus {
    outline: none;
  }


