.header_container {
  position: fixed;
  width: 100%;
}

.header {
    position: relative;
    height : 200px;
    background: #33363b ;
    text-align: center;
    margin-bottom: 60px;
  }
  
  .header .title {
    position: relative;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    font-size: 60px;
    margin: auto;
    color: white;
  }


