.inputGraber {
    /* float: left; */
    position: fixed;
    top: 40%;
    width: 25%;
    padding: 0 0 0 5%;
  }

.inputGraber input,
.inputGraber .addTask {
    display: block;
    margin: 20px auto;
    height: 44px;
}

.inputGraber input {
    border: 1px solid #ddd;
    font-size: 20px;
    width: 100%;
}

.inputGraber .addTask {
    font-size: 18px;
    background: #ddd;
    width: 101%;
    border: 2px solid #ddd;
    color: black;
    -webkit-transition: border 0.5s, background 0.5s, width 0.5s;
    -o-transition: border 0.5s, background 0.5s, width 0.5s;
    transition: border 0.5s, background 0.5s, width 0.5s;
}

.inputGraber .addTask:hover {
    border: 2px solid #004d80;
    background: white;
    color: #004d80;
    font-weight: bolder;
}  


