.dashboard {
  /* float: left; */
  position: absolute;
  top: 40%;
  right: 0;
  width: 60%;
  height: 60vh;
  overflow: auto;
  padding: 0 2.5%;
  z-index: -1;
  border-left: 1px solid #ccc;
}

.card {
  position: relative;
  border: 2px solid #eee;
  text-align: center;
  margin: 20px auto;
  height: 50px;
  width: 70%;
  -webkit-transition: height 0.1s, width 0.3s, border 0.3s, font-size 0.3s;
  -o-transition: height 0.1s, width 0.3s, border 0.3s, font-size 0.3s;
  transition: height 0.1s, width 0.3s, border 0.3s, font-size 0.3s;
}

.card p {
  position : absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  margin: 0 0;
  text-align: center;
}

.card:hover {
  border: 2px solid #004d80;
  font-size: 18px;
  height: 80px;
  width: 90%;
}

.name {
  font-size: 20px;
  color : #444 ;
}

.town {
  color : #999 ;
}


